<script>
import Basic from '../../At/Layouts/Basic'
import Skeleton from '../../At/Components/Skeleton'
import SwiperCard from '../../At/Components/SwiperCard'

import * as Request from '../../Js/requests'
export default {
	name: 'Youtuber',

    components: {Basic,Skeleton,SwiperCard,},
	methods: {
	
		setPerviewed: function(ip){ 		
	this.curPreviewed = ip

}
,
		getYoutubers: function() {		
	let arg = "_limit=" + this.youPerPage

	+ "&_start=" + ((this.curPage - 1) * this.youPerPage)

	

	Request.getTableArgs('youtubers', arg)

	.then((res) => {

		this.youtubers = res.data

		this.loading = false;

	}) 

}
,
	},
	mounted(){
			
	Request.getTable('youtubers/count')

	.then((res) => {

		this.youCount = res.data

	})

	

	this.getYoutubers()

	},
	computed: {
	
		numberPages: function() { 		
	return Math.ceil(this.youCount/this.youPerPage)

}
,
		filtredYou: function() { 		
	if(this.filter){

		return this.youtubers.filter(y => {

			let name = y.name.toLowerCase();

			let cname = y.channel_name.toLowerCase();

			let filter = this.filter.toLowerCase()

			

			return name.includes(filter)

						|| cname.includes(filter)

		})

	} else {

		return this.youtubers

	}

}
,
		secondColor: function() { 		
	return this.$store.state.secondColor

}
,
		foundNumber: function() { 		
		return '(' + this.filtredYou.length + ')'

}
,
	},
	watch: {
	
		curPage: function(val) { 		
	this.getYoutubers()

}
,
	},
	data(){
	return {
		
		youtubers:  []		,
		darkColor:  "#333333"		,
		filter:  null		,
		curPreviewed:  -2		,
		loading:  true		,
		youCount:  0		,
		youPerPage:  24		,
		curPage:  1		,
		}
	},
} 
</script>

