export function checkLang() {
  // Get the current path
  let path = router.history.current.path;
  // Get the current langauge in query
  let lang = router.history.current.query.lang;
  // Get the actual sectected language
  let selectedLang = store.state.selectedLang;

  // If there is a language props in the query
  if (lang) {
    if (lang !== selectedLang) {
      store.commit("changeSelectedLang", lang);
    }
  } else {
    // Else check if one language was already choosed in cache and pass it as query
    /*if(selectedLang){
			router.push({ path: path, query: { ...router.history.current.query, lang:  selectedLang} })			
		} else {
			// Else pass the defaut language
			router.push({ path: path, query: { ...router.history.current.query, lang:  i18n.fallbackLocale} })		
		}
		location.reload();*/
  }
}

export function reloadLangChange() {
  // Get the current path
  let path = router.history.current.path;
  // Get the actual sectected language
  let selectedLang = store.state.selectedLang;
  router.push({
    path: path,
    query: { ...router.history.current.query, lang: selectedLang },
  });
  location.reload();
}

export function validateEmail(email) {
  if (email == null || email == undefined) {
    return false;
  }
  if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(email)) {
    return true;
  }
  return false;
}

export function getCurrentTime() {
  let today = new Date();
  return today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
}

export function getVideoID(link) {
  let match = link.match(/v=([^&]+)/);
  return match[1];
}

export function mergeObject(obj1, obj2) {
  return { ...obj1, ...obj2 };
}

export function share(
  target,
  { url, title, hashtags, description, to, tags, subject }
) {
  console.log(url);
  switch (target) {
    case "facebook":
      fbButton({ url: url });
    case "twitter":
      tw({ url: url, title: title, hashtags });
    case "reddit":
      reddit({ url: url, title: title });
    case "pinterest":
      pinterest({ url: url, description: description });
    case "tumblr":
      tumblr({ url: url, tags: tags });
    case "vk":
      fbButton({ url: url });
    case "vk":
      fbButton({ url: url });
    case "ok":
      ok({ url: url });
    case "mail":
      mail({ url: url, title: title, description: description });
    case "email":
      email({
        to: to,
        url: url,
        title: title,
        description: description,
        subject: subject,
      });
    case "linkedin":
      linkedin({ url: url, title: title, description: description });
    case "whatsapp":
      whatsapp({ url: url, title: title });
    case "viber":
      viber({ url: url, title: title });
    case "telegram":
      telegram({ url: url, title: title });
    case "line":
      line({ url: url, title: title });
  }
}

export function getImgUrl(item, param = "image", size = "medium") {
  if (typeof item == "string") {
    return store.state.apiServer + item;
  }
  if (item && item[param]) {
    let url = store.state.apiServer;
    if (item[param].formats) {
      url += switchFormat(item, param, size);
    } else {
      url += item[param].url;
    }
    return url;
  } else {
    return "https://picsum.photos/1000";
  }
}

function switchFormat(item, param, size) {
  switch (size) {
    case "medium":
      return (
        item[param].formats.medium?.url ?? switchFormat(item, param, "small")
      );
    case "small":
      return (
        item[param].formats.small?.url ?? switchFormat(item, param, "large")
      );
    case "large":
      return (
        item[param].formats.large?.url ?? switchFormat(item, param, "thumbnail")
      );
    case "thumbnail":
      return item[param].formats.thumbnail?.url ?? switchFormat(item, param);
    default:
      return item[param].url;
  }
}

