<template>
 <div>  
<div id="Live"> 
         
 <div  :class="'rectangle rectangle0 '"   id="rectangle0" ref="rectangle0"  v-bind:style="{backgroundColor : darkBg,}">
 <div  :class="'item mainContent '"   id="mainContent" ref="mainContent"  v-bind:style="{top : getUnit(mainContentTop,'px'),}">
 <div  :class="'item liveZone '"   id="liveZone" ref="liveZone"  v-bind:style="{top : getUnit(isFullScreen? 0 : '20','px'),bottom : (mqVal({default:isFullScreen? 0 : (chatOpened? '50%' : 0),md:0+'px'}, $mq)),width : (mqVal({default:'100%',md:chatOpened? '65%' : '100%',lg:chatOpened? '75%' : '100%'}, $mq)),}">
 <LiveZone :class="'object livezoneOBJ '"   id="livezoneOBJ" ref="livezoneOBJ"  v-bind:style="{left : (mqVal({default:getUnit(isFullScreen? 0 : 10,'px'),md:getUnit(isFullScreen? 0 : 30,'px')}, $mq)),right : (mqVal({default:getUnit(isFullScreen? 0 : 10,'px'),md:getUnit(isFullScreen? 0 : 30,'px')}, $mq)),bottom : getUnit(isFullScreen? 0 : 180,'px'),}" :isFull = "isFullScreen" 
 :connections = "connections" 
>
</LiveZone>
 <div  :class="'item controlZone '"   id="controlZone" ref="controlZone" v-show="!isFullScreen"  v-bind:style="{}">
 <ControlZone :class="'object controlzonecontrolZone1 '"   id="controlzonecontrolZone1" ref="controlzonecontrolZone1"  v-bind:style="{}" :toggleChat = "toggleChat" 
 :showShare = "showShare" 
 :video = "currentVideo" 
>
</ControlZone>  </div>  </div>
 <div  :class="'item chatZone '"   id="chatZone" ref="chatZone" v-show="chatOpened"  v-bind:style="{top : (mqVal({default:getUnit(chatTop,'px'),md:getUnit((isFullScreen? 0 : 20),'px')}, $mq)),}">
 <ChatZone :class="'object chatzonechatZone1 '"   id="chatzonechatZone1" ref="chatzonechatZone1"  v-bind:style="{right : (mqVal({default:0+'px',md:getUnit(isFullScreen? 0 : 30,'px')}, $mq)),}" :hideChat = "toggleChat" 
 :isTyping = "isTyping" 
 :send = "send" 
 :toggleSideBar = "togleTopChat" 
 :isFull = "isFullScreen" 
 :typing = "typing" 
 :messages = "messages" 
 :chatExpended = "chatExpended" 
>
</ChatZone>  </div>  </div>
 <PinningHeader :class="'object pinningheaderrectangle03 '"   id="pinningheaderrectangle03" ref="pinningheaderrectangle03" v-show="(!isFullScreen)"  v-bind:style="{}" :openSideBae = "toggleSideBar" 
>
</PinningHeader>
 <Sidebar :class="'object sidebaritem033 '"   id="sidebaritem033" ref="sidebaritem033" v-if="(mqVal({default:sidebarVisible,md:false}, $mq))"  v-bind:style="{}" :closeSideBae = "toggleSideBar" 
>
</Sidebar>
 <ShareModal :class="'object sharemodalitem054 '"   id="sharemodalitem054" ref="sharemodalitem054" v-if="shareModalVi"  v-bind:style="{}" :close = "hideShare" 
 :url = "shareOptions.url" 
 :title = "shareOptions.title" 
