<script>
import LiveZone from '../../At/Sections/LiveZone'
import ControlZone from '../../At/Sections/ControlZone'
import ChatZone from '../../At/Sections/ChatZone'
import PinningHeader from '../../At/Sections/PinningHeader'
import Sidebar from '../../At/Sections/Sidebar'
import ShareModal from '../../At/Components/ShareModal'

import { io } from "socket.io-client";
import {getCurrentTime} from "../../Js/main"
export default {
	name: 'Live',

    components: {LiveZone,ControlZone,ChatZone,PinningHeader,Sidebar,ShareModal,},
	methods: {
	
		togleTopChat: function() { 		
	this.chatExpended = !this.chatExpended

}
,
		toggleSideBar: function() { 		
	this.sidebarVisible = !this.sidebarVisible

}
,
		toggleFullScreen: function() { 		
	if (!this.isFullScreen) {

		this.isFullScreen = true;

		this.chatOpened = false;

      document.documentElement.requestFullscreen();

  } else {

    if (document.exitFullscreen) {

			this.isFullScreen = false;

			this.chatOpened = true;

      document.exitFullscreen();

    }

  }

}
,
		toggleChat: function() { 		
	this.chatOpened =  !this.chatOpened

}
,
		isTyping: function(check) { 		
	check ? this.socket.emit('typing', this.userData.name) 

				: this.socket.emit('stopTyping')

}
,
		send: function(message) { 		
	let {username, image, id} = this.userData

	let msg = {

      message: message,

      user: {id: id, image: (image? image.url : null), name: username},

			time: getCurrentTime(),

	    type: 1,

  }

	

	this.messages.push(msg);

	

	this.socket.emit('chat-message', msg);

}
,
		connectToMaestro: function() { 		
	this.ready = true;

	let {username, image, id} = this.userData

	let msg = {

      user: {id: id, image: (image? image.url : null), name: username},

  }

	

  this.socket.emit('joined', msg)

}
,
		showShare: function() { 		
	this.shareModalVi = true;

}
,
		hideShare: function() { 		
	this.shareModalVi = false;

}
,
	},
	mounted(){
			
	if(this.userData == null){

		this.$router.push('/')

		return

	}

	

	this.socket = io(this.$store.state.maestroServer, {

     transports: ['websocket', 'polling', 'flashsocket'],

  });

	

	window.onbeforeunload = () => {

  	this.socket.emit('leave', this.userData.name);

  }		

	

	this.socket.on('chat-message', (data) => {

	    this.messages.push(data);

	});	

	

	this.socket.on('chat-messages', (data) => {

	    this.messages = data ?? [];

	});

	

	this.socket.on('video', (data) => {

	    this.currentVideo = data ?? ({});

	});

	

	this.socket.on('typing', (data) => {

	    this.typing = data;

	});

	

	

	this.socket.on('stopTyping', () => {

	    this.typing = false;

	});

	

	this.socket.on('joined', (data) => {

	    this.messages.push({

	        user: data.user,

	        type: 'joined'

	    });

	});

	

	this.socket.on('connections', (data) => {

	    this.connections = data;

	});

	

	this.connectToMaestro()

	

	let video = this.$refs.livezoneOBJ

		.$refs.videoPlayer

		.$el.querySelector('video')

	

	video.addEventListener('fullscreenchange', (event) => {

	  if (document.fullscreenElement) {

	    this.isFullScreen = true;

	  } else {

	    this.isFullScreen = false;

	  }

	});

	},
	computed: {
	
		darkBg: function() { 		
	return this.$store.state.darkBg

}
,
		chatTop: function() { 		
	if(this.chatExpended){

		return 0

	} else {

		return '50%'

	}

}
,
		mainContentTop: function() { 		
	if(this.isFullScreen){

		return 0

	} else {

		return 60

	}

}
,
		userData: function() { 		
	return this.$store.state.user

}
,
		shareOptions: function() { 		
	return {

		url: 'https://okike.tv/live', 

		title: 'okikeTV | ' + this.currentVideo.title, 

		hashtags: ['live', 

			this.currentVideo.title, 

			this.currentVideo.slug], 

		description: 'Regardez en live' + this.currentVideo.title + 'sur okikeTV', 

		to: '',

		subject: 'Regardez en live' + this.currentVideo.title + 'sur okikeTV'

	}

}
,
	},
	data(){
	return {
		
		isFullScreen:  false		,
		chatOpened:  true		,
		chatExpended:  false		,
		sidebarVisible:  false		,
		typing:  false		,
		ready:  false		,
		connections:  0		,
		messages:  []		,
		currentVideo:  ({})		,
		shareModalVi:  false		,
		}
	},
} 
</script>

