<script>

import * as Request from '../../Js/requests'
export default {
	name: 'ControlZone',

    components: {},
	methods: {
	
		getYoutuber: function() { 		
	Request.getTableArgs('youtubers', 

	'id=' + this.video.youtuber)

	.then((res) => {

		this.youtuber = res.data[0]

	})

}
,
		signToNotif: function(){ 		


}
,
	},
	computed: {
	
		firstColor: function() { 		
	return this.$store.state.firstColor

}
,
		vidTitel: function() { 		
	return (this.video.title?  

		this.video.title:

		"..."

	)

}
,
		link: function() { 		
	return '/youtuber/' + this.youtuber.slug

}
,
		vidViews: function() { 		
	return (this.video.fetchedData?  

		this.video.fetchedData.views + ' ' + this.$t('Profile.views'):

		"..."

	)

}
,
		vidOutDate: function() { 		
	return (this.video.fetchedData?  

		this.$t('Profile.outDate') + ' ' + this.video.fetchedData.datePublished:

		"..."

	)

}
,
		vidTags: function() { 		
	return (this.video.fetchedData?

		this.video.fetchedData.genre.match(/\w+/g):

		['...']

	) 

}
,
		youName: function() { 		
	return this.youtuber.name ?? '...'

}
,
		youImg: function() { 		
	return (this.youtuber.image? 

		this.$store.state.apiServer + this.youtuber.image.url:

		"https://picsum.photos/400"

	)

}
,
	},
	watch: {
	
		quality: function(val) { 		
	this.$store.commit('setQualityV', this.quality)

}
,
		video: function(val){ 		
	this.getYoutuber()

}
,
	},
	data(){
	return {
		
		youtuber:  ({})		,
		quality:  this.$store.state.qualityV		,
		qList:  ['720', '480', '360']		,
		}
	},
	props: {
	
		video: {					required: false,			default: () =>  ({}),		},
