 <div>  
<div id="Legal"> 
         
 <Basic :class="'object basic0 '"   id="basic0" ref="basic0"  v-bind:style="{}">
 <template  v-slot:content>
 <div  :class="'slottemplate slottemplatebasic01 '"   id="slottemplatebasic01" ref="slottemplatebasic01"  v-bind:style="{}">
 <div  :class="'column mainContent '"   id="mainContent" ref="mainContent"  v-bind:style="{}">
 <div  :class="'column content '"   id="content" ref="content"  v-bind:style="{}">
 <div  :class="'row rowcontent1 '"   id="rowcontent1" ref="rowcontent1"  v-bind:style="{justifyContent : 'flex-end',}">
 <v-btn tag="div" 
 :outlined = "true" 
 :dark = "true" 
 @click="print" :class="'button printBtn '"   id="printBtn" ref="printBtn"  v-bind:style="{}"><v-icon  :style = "{}"  v-if = "'ion-md-print' !== undefined"  :left = "true" 
>{{ 'ion-md-print' }}
</v-icon><span>{{ 'PRINT' }}</span>
</v-btn>  </div>
 <div  :class="'text textContent '"   id="textContent" ref="textContent"  v-bind:style="{color : '#fff',}">
<div class="text-content"></div>

   </div>
