<script>
import Basic from '../../At/Layouts/Basic'

import * as Request from '../../Js/requests'
import * as Main from '../../Js/main'
import {marked} from 'marked'
import { Printd } from 'printd'
export default {
	name: 'Legal',

    components: {Basic,},
	methods: {
	
		print: function() { 		
	const cssText = `

 	 .main {

	    background-color: #141414;

			color: #fff;

	  }

	`

	

	// define options to use

	const options = {

	  parent: document.getElementById('textContent')

	}

	const d = new Printd(options);

	d.print( document.getElementById('textContent'), [ cssText ] );

}
,
		loadArchive: function() { 		
	Request.getArchive(this.page)

	.then(res => {

		this.archive = res.data[0]

		document.getElementById('textContent').innerHTML 

			= `<div class="main">${marked.parse(this.archive.content)}</div>`;

	})

	.catch(error => {

		console.log(error)

	})

}
,
	},
	mounted(){
			
	this.page = this.$route.params.page

	

	if(this.page == undefined){

		this.$router.push('/')

	}

	this.loadArchive()	

	},
	computed: {
	
		selectedLang: function() { 		
	return this.$store.state.selectedLang

}
,
		lastActualisation: function() { 		
	if(this.archive){

		return this.$t('Legal.lastActualisation') + this.archive.updated_at

	}

	return "-- : --"

}
,
	},
	watch: {
	
		selectedLang: function() { 		
	this.loadArchive()

}
,
	},
	data(){
	return {
		
		content:  ''		,
		archive:  ""		,
		page:  undefined		,
		}
	},
} 
</script>

