<script>
import Basic from '../../At/Layouts/Basic'
import ArticleBoxMini from '../../At/Components/ArticleBoxMini'

import * as Request from '../../Js/requests'
import * as Main from '../../Js/main'
import {marked} from 'marked'
import { Printd } from 'printd'
export default {
	name: 'BlogDetail',

    components: {Basic,ArticleBoxMini,},
	methods: {
	
		getArticle: function() { 		
	Request.getLocalTableArgs('articles', 

	'slug_contains=' + this.slug)

	.then((res) => {

		this.article = res.data[0]

		document.getElementById('textContent').innerHTML 

			= `<div class="main">${marked.parse(this.article.content)}</div>`;

			

		this.getArticles()

	})

}
,
		getArticles: function() {		
	let arg = "id_ne=" + this.article.id

	+ "&_limit=5"

	+ "&_sort=id:DESC"

	

	Request.getLocalTableArgs('articles', arg)

	.then((res) => {

		this.articles = res.data

	}) 

}
,
	},
	mounted(){
			
	this.getArticle()

	},
	computed: {
	
		secondColor: function() { 		
	return this.$store.state.secondColor

}
,
		selectedLang: function() { 		
	return this.$store.state.selectedLang

}
,
		title: function() { 		
	return this.article.title

}
,
		description: function() { 		
	return this.article.description

}
,
		redactorName: function() { 		
	return this.article.redactor.username

}
,
		redactorImage: function() { 		
	let item = this.article.redactor;

	return Main.getImgUrl(item, 'image', 'small');

}
,
		date: function() { 		
	return this.article.published_at

}
,
		image: function() { 		
	let item = this.article;

	return Main.getImgUrl(item, 'imageDescription', 'small');

}
,
		tag: function() { 		
	return (this.article.tag?

		this.article.tag.toUpperCase()

		: ""

	)

}
,
		link: function() { 		
	return '/blog/' + this.slug

}
,
		redactorName: function() { 		
	if(this.article.redactor){

		return this.article.redactor.username

	}

	return ""

}
,
		redactorImage: function() { 		
	let item = this.article.redactor;

	return Main.getImgUrl(item, 'image', 'small');

}
,
		date: function() { 		
	return this.article.published_at

}
,
		media: function() { 		
	if(this.article.multimedia){

		return this.article.multimedia.map(media => {	

			return Main.getImgUrl({image: media}, 'image', 'medium');

		})

	}

	return []

}
,
		slug: function() { 		
	return this.$route.params.slug

}
,
	},
	watch: {
	
		selectedLang: function() { 		
	this.getArticles()

}
,
		slug: function(val) { 		
	location.reload();

}
,
	},
	data(){
	return {
		
		article:  ({})		,
		articles:  []		,
		darkColor:  "#333333"		,
		}
	},
} 
</script>

