<script>
import LeftMessage from '../../At/Components/LeftMessage'
import RightMessage from '../../At/Components/RightMessage'
import InfoMessage from '../../At/Components/InfoMessage'

import { VEmojiPicker } from 'v-emoji-picker';
export default {
	name: 'ChatZone',

    components: {LeftMessage,RightMessage,InfoMessage,VEmojiPicker,},
	methods: {
	
		togleEmoji: function() { 		
	this.emojiVisible = !this.emojiVisible

}
,
		insertEmoji: function(e){		
	this.newMessage = (this.newMessage ?? '') + e.data;

}
,
		scrollToBottom: function() { 		
	let sArea = document.querySelector('.mainScroll')

	sArea.scrollTop = sArea.scrollHeight - sArea.clientHeight

	this.notBottom = false

}
,
		checkLeft: function(item) { 		
	return item.user && (item.type == 1) &&(item.user.id != this.userData.id)

}
,
		checkRight: function(item) { 		
	return item.user && (item.type == 1) &&(item.user.id == this.userData.id)

}
,
		sendMessage: function() { 		
	this.send(this.newMessage)

	this.newMessage = null

}
,
	},
	mounted(){
			
	this.scrollToBottom()

	},
	computed: {
	
		chatModes: function() { 		
	return [

		this.$t('ChatZone.liveChat'),

		this.$t('ChatZone.privateChat')

	]

}
,
		user: function(){ 		
	return this.$store.state.user ?? ({});

}
,
		darkBg: function() { 		
	return this.$store.state.darkBg

}
,
		lessDarkBg: function() { 		
	return this.$store.state.lessDarkBg

}
,
		firstColor: function() { 		
	return this.$store.state.firstColor

}
,
		secondColor: function() { 		
	return this.$store.state.secondColor

}
,
		thirdColor: function() { 		
	return this.$store.state.thirdColor

}
,
		userData: function() { 		
	return this.$store.state.user

}
,
		image: function(){ 		
	return (this.userData.image? 

		this.$store.state.apiServer + this.userData.image.url:

		"https://picsum.photos/400"

	)

}
,
		userName: function() { 		
	return this.userData.username ?? '...'

}
,
		expendIcon: function() { 		
	return (this.chatExpended? 

		'ion-ios-arrow-down' : 

		'ion-ios-arrow-up')

}
,
	},
	watch: {
	
		newMessage: function(value){		
	return value? this.isTyping(true) : this.isTyping(false)

}
,
		messages: function(val) { 		
	let sArea = document.querySelector('.mainScroll')

	let check = (sArea.scrollTop + sArea.clientHeight 

					!= sArea.scrollHeight)

	this.notBottom = check

}
,
	},
	data(){
	return {
		
		darkColor:  "#202020"		,
		newMessage:  null		,
		emojiVisible:  false		,
		chatMode:  this.$t('ChatZone.liveChat')		,
		notBottom:  false		,
		}
	},
	props: {
	
		isFull: {					required: false,			default: () =>  false,		},
		typing: {					required: false,			default: () =>  null,		},
		hideChat: {			type: Function,			required: false,			default: () =>  (()=>{}),		},
		isTyping: {			type: Function,			required: false,			default: () =>  (()=>{}),		},
		send: {			type: Function,			required: false,			default: () =>  (()=>{}),		},
		toggleSideBar: {			type: Function,			required: false,			default: () =>  (()=>{}),		},
		messages: {					required: false,			default: () =>  [] ,		},
		chatExpended: {					required: false,			default: () =>  false,		},
	},
} 
</script>

