<script>
import Basic from '../../At/Layouts/Basic'
import SwiperRow from '../../At/Utils/SwiperRow'
import CallToAction from '../../At/Utils/CallToAction'
import CardWall from '../../At/Utils/CardWall'
import TitleText from '../../At/Components/TitleText'
import TileWrap from '../../At/Utils/TileWrap'
import PartnerCard from '../../At/Utils/PartnerCard'

import * as Request from "../../Js/requests"
import * as Main from '../../Js/main'
export default {
	name: 'Home',

    components: {Basic,SwiperRow,CallToAction,CardWall,TitleText,TileWrap,PartnerCard,},
	methods: {
	
		gotoLive: function() { 		
	this.$router.push('/live')

}
,
		getHomePage: function() { 		
	Request.getLocalTable('home-page')

	.then((res) => {

		this.hpData = res.data

	})

}
,
	},
	mounted(){
			
	this.getHomePage()

	

	Request.getTable('youtubers')

	.then((res) => {

		this.youtubers = res.data

	})

	

	Request.getLocalTable('categories')

	.then((res) => {

		this.categories = res.data

	})

	

	Request.getTable('sponsors')

	.then((res) => {

		this.sponsors = res.data

	})

	},
	computed: {
	
		selectedLang: function() { 		
	return this.$store.state.selectedLang

}
,
		darkBg: function() { 		
	return this.$store.state.darkBg

}
,
		bannerLink: function() { 		
	if(this.hpData.banner){

		return [this.$store.state.apiServer + this.hpData.banner.url]

	}

	return []

}
,
		devices: function() { 		
	let item = this.hpData;

	return Main.getImgUrl(item, 'multidevices', 'original');

}
,
		bannerPoster: function() { 		
	let item = this.hpData;

	return Main.getImgUrl(item, 'banner_poster', 'original');

}
,
		tileContent: function(){		
	return [

		{

			icon: "ion-ios-videocam",

			title: this.$t('home.tileTitle1'),

			message: this.$t('home.tileContent1')

		},

		{

			icon: "ion-md-hand",

			title: this.$t('home.tileTitle2'),

			message: this.$t('home.tileContent2')

		},

		{

			icon: "ion-md-medkit",

			title: this.$t('home.tileTitle3'),

			message: this.$t('home.tileContent3')

		},

	]

}
,
		filtYou: function() { 		
	let list = this.youtubers.map(y => {

		let total = 0;

		for(let vid of y.videos){

			total += (vid.views ?? 0)

		}

		y.totalViews = total

		return y

	});

	list = list.sort((y1, y2) => {

		return (y2.totalViews - y1.totalViews)

	})

	return list

}
,
	},
	watch: {
	
		selectedLang: function() { 		
	this.getHomePage()

}
,
		categories: function(){ 		
	let list = []

	let count = this.categories.length

	

	let finished = false

	let i = 0;

	

	while(!finished){

		if(count <= 7){

			finished = true

			list.push(this.categories.slice(i))

		} else {

			list.push(this.categories.slice(i, i+7))

			i += 7

			count -= 7

		}

	}

	this.catSections = list

}
,
	},
	data(){
	return {
		
		youtubers:  []		,
		categories:  []		,
		catSections:  []		,
		sponsors:  []		,
		hpData:  []		,
		catModelVisible:  false		,
		currentCatSlug:  null		,
		}
	},
} 
</script>

