<script>
import Basic from '../../At/Layouts/Basic'
import TeamMember from '../../At/Components/TeamMember'

import * as Request from "../../Js/requests"

import copy from 'copy-to-clipboard';
export default {
	name: 'About',

    components: {Basic,TeamMember,},
	methods: {
	
		copyIt: function(info) { 		
	copy(info)

}
,
		getAbout: function() { 		
	Request.getLocalTable('about')

	.then((res) => {

		this.about = res.data

	})	

}
,
	},
	mounted(){
			
	this.getAbout()

	

	Request.getLocalTableArgs('team-members',

	'_sort=id')

	.then((res) => {

		this.members = res.data

	})

	Request.getTable('contact')

	.then((res) => {

		this.contact = res.data

	})

	},
	computed: {
	
		selectedLang: function() { 		
	return this.$store.state.selectedLang

}
,
		darkBg: function() { 		
	return this.$store.state.darkBg

}
,
		lessDarkBg: function() { 		
	return this.$store.state.lessDarkBg

}
,
		firstColor: function() { 		
	return this.$store.state.firstColor

}
,
		secondColor: function() { 		
	return this.$store.state.secondColor

}
,
		contactList: function() { 		
	return [

		{

			name: 'Email',

			info: this.contact.mail

		},

		{

			name: this.$t('About.telNumber'),

			info: this.contact.number

		},

		{

			name: this.$t('About.whatsappNumber'),

			info: this.contact.whatsapp_number

		},

	]

}
,
	},
	watch: {
	
		selectedLang: function() { 		
	this.getAbout()

}
,
	},
	data(){
	return {
		
		members:  []		,
		contact:  ({})		,
		about:  ({})		,
		}
	},
} 
</script>

