import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from "vuex-persistedstate";
/**
 * Please don' t remove this.
 * Artefact will use it to inject code
 */
//<= inject_import =>

//<= end_import =>

Vue.use(Vuex)

export default new Vuex.Store({
  plugins: [createPersistedState()],
  /**
   * Please don' t remove this.
   * Artefact will use it to inject code
   */
  //<= inject_store =>
state: {
	
  
		appName:  "okikeTV"		,
		firstColor:  "#FCBE00"		,
		secondColor:  "#FF6000"		,
		thirdColor:  "#D70E5A"		,
		darkBg:  "#141414"		,
		lessDarkBg:  "#2b2c2c"		,
		language:  ['fr', 'en', 'de']		,
		selectedLang:  'fr'		,
		user:  null		,
		token:  null		,
		apiServer:  "https://api.okike.tv"		,
		liveServer:  "https://live.okike.tv"		,
		maestroServer:  "https://maestro.okike.tv"		,
		qualityV:  '480'		,      },

        mutations: {

   
		changeSelectedLang: function(state, val) { 		
	state.selectedLang = val

}
,
		changeUser: function(state, val) { 		
	state.user = val

}
,
		changeToken: function(state, val) { 		
	state.token = val

}
,
		setQualityV: function(state, val) { 		
	state.qualityV = val

}
,     },

        actions: {

   
		login: function({ commit }, {user, jwt}) { 		
	commit('changeUser', user)

	commit('changeToken', jwt)

}
,
		logout: function({ commit }) { 		
	commit('changeUser', null)

	commit('changeToken', null)

}
,     },

        
//<= end_store =>
})
