<template>
 <div>  
<div id="LiveZoneDASH"> 
         
<div class="artefactVid"  :class="'networkvideo videoPlayer '"   id="videoPlayer" ref="videoPlayer"  v-bind:style="{}"> <video width="100%" height="100%"  :controls = "false" 
 :preload = "'auto'" 
>
</video> </div>
 
      </div>
 </div> 
</template>
<script>

import { MediaPlayer } from 'dashjs';
export default {
	name: 'LiveZoneDASH',

    components: {},
	methods: {
	
		playVideo: function(source) {		
	this.player.initialize(this.video, source, true);

	this.player.updateSettings(this.playerOptions);

  this.video.muted = false;

	this.player.setVolume(this.volume/100)

}
,
	},
	mounted(){
			
	this.player.on(MediaPlayer.events.CAN_PLAY, (data)=>{

      this.player.play();

  });

	this.player.on(MediaPlayer.events.PLAYBACK_NOT_ALLOWED, (data)=>{

      this.video.muted = true;

      this.player.play();

  });

	

	const src = this.liveLink

  this.playVideo(src)

	},
	computed: {
	
		liveLink: function() { 		
	return this.$store.state.liveServer + '/live/okikeLive/index.mpd'

}
,
		player: function() {		
	return MediaPlayer().create();

}
,
		video: function() {		
	return document.querySelector('.videoPlayer video');

}
,
	},
	watch: {
	
		volume: function(val) { 		
	this.player.setVolume(val/100)

}
,
	},
	data(){
	return {
		
		playerOptions:  {		
	streaming: {	

	  delay: {

	      liveDelay: 4

	  },

	  liveCatchup: {

	      enabled: true

	  }

 	}

}
,
		}
	},
	props: {
	
		isFull: {					required: false,			default: () =>  false,		},
		volume: {					required: false,			default: () =>  100,		},
	},
} 
</script>

