<script>
import Basic from '../../At/Layouts/Basic'
import VideoCard from '../../At/Utils/VideoCard'
import ShareModal from '../../At/Components/ShareModal'

import * as Request from '../../Js/requests'
import * as Main from '../../Js/main'
export default {
	name: 'YoutuberDetail',

    components: {Basic,VideoCard,ShareModal,},
	methods: {
	
		getVideos: function() { 		
	Request.getTableArgs('videos', 

	'youtuber=' + this.youtuber.id

	+ "&_limit=" + this.vidPerPage

	+ "&_start=" + ((this.curPage - 1) * this.vidPerPage))

	.then((res) => {

		this.videos = res.data

	})

}
,
		getVideosCount: function() { 		
	Request.getTableArgs('videos/count',

	'youtuber=' + this.youtuber.id)

	.then((res) => {

		this.vidCount = res.data

	})

}
,
		showShare: function() { 		
	this.shareModalVi = true;

}
,
		hideShare: function() { 		
	this.shareModalVi = false;

}
,
	},
	mounted(){
			
	let slug = this.$route.params.slug

	

	Request.getTableArgs('youtubers', 

	'slug=' + slug)

	.then((res) => {

		this.youtuber = res.data[0]

		this.getVideosCount()

		this.getVideos()

	})

	},
	computed: {
	
		numberPages: function() { 		
	return Math.ceil(this.vidCount/this.vidPerPage)

}
,
		darkBg: function() { 		
	return this.$store.state.darkBg

}
,
		secondColor: function() { 		
	return this.$store.state.secondColor

}
,
		image: function() { 		
	let item = this.youtuber;

	return Main.getImgUrl(item);

}
,
		shareOptions: function() { 		
	return {

		url: 'https://okike.tv/youtuber/' + this.youtuber.slug, 

		title: 'okikeTV | ' + this.youtuber.name, 

		hashtags: ['youtuber', this.youtuber.name, this.youtuber.slug], 

		description: this.youtuber.name + 'sur okikeTV', 

		to: '',

		subject: this.youtuber.name + 'sur okikeTV'

	}

}
,
		teaserLink: function() { 		
	if(this.youtuber.teaser){

		return [this.$store.state.apiServer + this.youtuber.teaser.url]

	}

	return []

}
,
	},
	watch: {
	
		curPage: function(val) { 		
	this.getVideos()

}
,
	},
	data(){
	return {
		
		youtuber:  ({})		,
		videos:  []		,
		shareModalVi:  false		,
		vidCount:  0		,
		vidPerPage:  24		,
		curPage:  1		,
		}
	},
} 
</script>

