<template>
 <div>  
<div id="Landing_signZone"> 
         
 <div  :class="'column mailCol52 '"   id="mailCol52" ref="mailCol52"  v-bind:style="{}">
 <div  :class="'text textcolumnitemmainContent131231 '"   id="textcolumnitemmainContent131231" ref="textcolumnitemmainContent131231"  v-bind:style="{color : '#fff',}">
<div class="text-content">{{ $t('Landing.callToSignup') }}</div>

