 <v-text-field  :placeholder = "$t('Landing.email')" 
 :class="'textfield emailField '"   id="emailField" ref="emailField" v-model="email" :type = "'email'" 
  v-bind:style="{color : secondColor,}"></v-text-field>  </div>
 <v-btn tag="div" 
 :color = "secondColor" 
 :dark = "true" 
 @click="signIn" :class="'button buttonflowcolumncolumnitemmainContent13273 '"   id="buttonflowcolumncolumnitemmainContent13273" ref="buttonflowcolumncolumnitemmainContent13273"  v-bind:style="{}"><span>{{ $t('Landing.getStarted') }}</span>
</v-btn>  </div>
 <div  :class="'text errorMessage '"   id="errorMessage" ref="errorMessage" v-if="errorVisible"  v-bind:style="{color : '#ff1a05',}">
<div class="text-content">{{ error }}</div>

