<script>


import * as Main from '../../../Js/main'
export default {
	name: 'Landing_signZone',

    components: {},
	methods: {
	
		showError: function(message) { 		
	this.error = message

	this.errorVisible = true	

}
,
		signIn: function(){ 		
	if(this.email){

		if(Main.validateEmail(this.email)){

			this.$router.push(`/signup/${this.email}`)

		} else {

			this.showError(this.$t('Landing.provideGoodMail'))

		}

	} else {

		this.showError(this.$t('Landing.emailError'))

	}

}
,
	},
	computed: {
	
		secondColor: function() { 		
	return this.$store.state.secondColor

}
,
	},
	data(){
	return {
		
		errorVisible:  false		,
		error:  "..."		,
		email:  null		,
		}
	},
} 
</script>

