<script>
import Basic from '../../At/Layouts/Basic'

import * as Request from '../../Js/requests'
import * as Main from '../../Js/main'
export default {
	name: 'Login',

    components: {Basic,},
	methods: {
	
		toggelPass: function() { 		
	this.showPass = !this.showPass

}
,
		loginGoogle: function() {		
	Request.signinGoogle()

}
,
		loginFacebook: function() {		
	Request.signinFacebook()

}
,
		showError: function(message) { 		
	this.error = message

	this.errorVisible = true	

}
,
		login: function() { 		
	// check email

	if(!Main.validateEmail(this.email)){

		this.showError(this.$t('Landing.provideGoodMail'))	

		return

	}

	// check pass

	if(this.pass == null){

		this.showError(this.$t('Signup.enterPass'))		

		return

	}	

	

	Request.login(this.email, this.pass)

	.then(res => {

		let {user, jwt} = res.data

		this.$store.dispatch('login', {user, jwt})

		this.$router.push('/')

	})

	.catch(error => {

		this.showError(error)

	})

}
,
		getLoginPage: function() { 		
	Request.getTable('login-page')

	.then((res) => {

		this.lpData = res.data

	})

}
,
	},
	mounted(){
			
	this.getLoginPage()

	if(this.$store.state.user){

		this.$router.push('/')

	}

	},
	computed: {
	
		banner: function() { 		
	let item = this.lpData;

	return Main.getImgUrl(item, 'banner');

}
,
		passIcon: function(){		
	if(this.showPass){

		return 'ion-ios-eye'

	} else {

		return 'ion-ios-eye-off'

	}

}
,
		secondColor: function() { 		
	return this.$store.state.secondColor

}
,
	},
	data(){
	return {
		
		email:  null		,
		pass:  null		,
		errorVisible:  false		,
		error:  "..."		,
		showPass:  false		,
		lpData:  ({})		,
		}
	},
} 
</script>

