<template>
 <div>  
<div id="LiveZoneHLS"> 
         
 <videoPlayer :class="'importedobject videoPlayer ' + 'vjs-custom-skin' + 'video-js vjs-fluid vjs-live'"   id="videoPlayer" ref="videoPlayer" :options = "playerOptions" 
@play = "onPlayerPlay($event)" 
@ready = "onPlayerReady($event)" 
  v-bind:style="{}">
</videoPlayer>
 
      </div>
 </div> 
</template>
<script>

import videoPlayer from 'vue-videojs7/src/components/VideoPlayer'
import 'videojs-hls-quality-selector'
export default {
	name: 'LiveZoneHLS',

    components: {videoPlayer,},
	methods: {
	
		onPlayerPlay: function(player) {		
  console.log('player play!', player)

}
,
		onPlayerReady: function(player) {		
  console.log('player ready!', player)

  this.player.play()

}
,
		playVideo: function(source) {		
 	const video = {

	 withCredentials: false,

   //type: 'application/x-mpegurl',

   type: 'application/dash+xml',

   src: this.liveLink

 }

 this.player.reset() // in IE11 (mode IE10) direct usage of src() when <src> is already set, generated errors,

 this.player.src(video)

 // this.player.load()

 this.player.play()

 this.player.muted(false)

	this.player.volume(this.volume)

	this.player.hlsQualitySelector({

    displayCurrentQuality: true,

	});

}
,
	},
	mounted(){
			
  this.playVideo()

	},
	computed: {
	
		liveLink: function() { 		
	//return this.$store.state.liveServer + '/live/okikeLive/index.m3u8'

	return this.$store.state.liveServer + '/live/okikeLive/index.mpd'

}
,
		player: function() {		
	return this.$refs.videoPlayer.player;

}
,
	},
	watch: {
	
		volume: function(val) { 		
	this.player.volume(val)

}
,
	},
	data(){
	return {
		
		playerOptions:  {		
	autoplay: true,

  controls: true,

	liveui:true,

	muted: true,

	fill: true

  // poster: 'https://surmon-china.github.io/vue-quill-editor/static/images/surmon-5.jpg'

}
,
		}
	},
	props: {
	
		isFull: {					required: false,			default: () =>  false,		},
		volume: {					required: false,			default: () =>  100,		},
		quality: {					required: false,			default: () =>  '',		},
	},
} 
</script>

