<script>
import PreviewModal from '../../At/Components/PreviewModal'

import * as Main from '../../Js/main'
export default {
	name: 'SwiperCard',

    components: {PreviewModal,},
	methods: {
	
		showPreview: function() { 		
	this.setPerviewed(this.ip)

}
,
		hidePreview: function() { 		
	this.setPerviewed(null)

}
,
		showDetail: function() { 		
	this.$router.push(this.link)

}
,
	},
	computed: {
	
		previewVisible: function(){		
	return this.ip == this.curPreviewed

}
,
		youImg: function() { 		
	let item = this.youInfo;

	return Main.getImgUrl(item, 'image', 'small');

}
,
		link: function() { 		
	return '/youtuber/' + this.youInfo.slug

}
,
	},
	props: {
	
