<script>
import Basic from '../../At/Layouts/Basic'

import * as Request from '../../Js/requests'
import * as Main from '../../Js/main'
export default {
	name: 'Signup',

    components: {Basic,},
	methods: {
	
		toggelPass: function() { 		
	this.showPass = !this.showPass

}
,
		showError: function(message) { 		
	this.error = message

	this.errorVisible = true	

}
,
		signup: function() { 		
	if(this.name == null){

		this.showError(this.$t('Signup.enterName'))		

		return

	}	

	// check email

	if(!Main.validateEmail(this.email)){

		this.showError(this.$t('Landing.provideGoodMail'))	

		return

	}

	// check pass

	if(this.pass == null){

		this.showError(this.$t('Signup.enterPass'))		

		return

	}	

	if(this.pass2 == null){

		this.showError(this.$t('Signup.enterPassConf'))		

		return

	}	

	if(this.pass != this.pass2){

		this.showError(this.$t('Signup.samePass'))		

		return

	}	

	

	Request.signup(this.name, this.email, this.pass)

	.then(res => {

		let {user} = res.data

		Request.sendMailConfirmation(user.email)

		.then(res => {

			this.confirmMailVisible = true

		})

		.catch(error => {

			this.showError(Main.getErrorMessage(error))

		})

	})

	.catch(error => {

		this.showError(Main.getErrorMessage(error))

	})

}
,
		getSignupPage: function() { 		
	Request.getTable('login-page')

	.then((res) => {

		this.sData = res.data

	})

}
,
	},
	mounted(){
			
	this.getSignupPage()

	

	if(this.$store.state.user){

		this.$router.push('/')

	}

	

	if(this.$store.state.user){

		this.$router.push('/')

	}

	if(this.$route.params.email){

		this.email = this.$route.params.email

	}

	},
	computed: {
	
		banner: function() { 		
	let item = this.sData;

	return Main.getImgUrl(item, 'banner');

}
,
		passIcon: function(){		
	if(this.showPass){

		return 'ion-ios-eye'

	} else {

		return 'ion-ios-eye-off'

	}

}
,
		secondColor: function() { 		
	return this.$store.state.secondColor

}
,
	},
	data(){
	return {
		
		name:  null		,
		email:  null		,
		pass:  null		,
		pass2:  null		,
		errorVisible:  false		,
		error:  "..."		,
		showPass:  false		,
		confirmMailVisible:  false		,
		sData:  ({})		,
		}
	},
} 
</script>

