<script>
import Basic from '../../At/Layouts/Basic'
import VideoCard from '../../At/Utils/VideoCard'
import ShareModal from '../../At/Components/ShareModal'

import * as Request from '../../Js/requests'
import * as Main from '../../Js/main'
import qs from 'qs'
export default {
	name: 'Category',

    components: {Basic,VideoCard,ShareModal,},
	methods: {
	
		gotoLive: function() { 		
	this.$router.push('/live')

}
,
		update: function() { 		
	let id = this.categorie.id

	

	let localId = this.categorie.localizations.map(c => c.id)

	localId.push(id)

	

	const query = qs.stringify({ _where: { category_in: localId} });

	Request.getTableArgs('videos', query)

	.then((res) => {

		this.videos = res.data

	})

}
,
		getData: function(id) { 		
	Request.getCatInfos(id)

	.then((res) => {

		this.categorie = res.data[0]

	})

}
,
		showShare: function() { 		
	this.shareModalVi = true;

}
,
		hideShare: function() { 		
	this.shareModalVi = false;

}
,
	},
	mounted(){
			
	if(this.$route.params.cat){

		this.getData(this.$route.params.cat)

	}

	},
	computed: {
	
		secondColor: function() { 		
	return this.$store.state.secondColor

}
,
		image: function() { 		
	let item = this.categorie;

	return Main.getImgUrl(item)

}
,
		diffusionTime: function() { 		
	return this.$t('Category.diffPlage')

		+ this.categorie.broadcast_time

		+ ' - '

		+ this.categorie.broadcast_end

}
,
		description: function() { 		
	return this.categorie.description ?? '...'

}
,
		shareOptions: function() { 		
	return {

		url: 'https://okike.tv/category/' + this.categorie.slug, 

		title: 'okikeTV | ' + this.categorie.name, 

		hashtags: ['category', this.categorie.name, this.categorie.slug], 

		description: 'Regardez ' + this.categorie.name + 'sur okikeTV', 

		to: '',

		subject: 'Regardez ' + this.categorie.name + 'sur okikeTV'

	}

}
,
		slogan: function() { 		
	return this.$t('Category.envy') + this.categorie.name

}
,
	},
	watch: {
	
		categorie: function() { 		
	this.update()

}
,
	},
	data(){
	return {
		
		categorie:  ({})		,
		videos:  []		,
		shareModalVi:  false		,
		}
	},
} 
</script>

