<template>
 <div>  
<div id="LiveZoneFLV"> 
         
 <div  :class="'item videoContainer '"   id="videoContainer" ref="videoContainer"  v-bind:style="{}">
<div class="artefactVid"  :class="'networkvideo videoPlayer '"   id="videoPlayer" ref="videoPlayer"  v-bind:style="{}"> <video width="100%" height="100%"  :controls = "true" 
 :preload = "'auto'" 
>
</video> </div>  </div>
 
      </div>
 </div> 
</template>
<script>

import flvjs from 'flv.js';
export default {
	name: 'LiveZoneFLV',

    components: {},
	methods: {
	
		flv_destroy: function() {			
	this.player.pause();

	this.player.unload();

	this.player.detachMediaElement();

	this.player.destroy();

	this.player = null;

}
,
		playVideo: function() {		
	this.player = flvjs.createPlayer({

      type: 'flv',

      url: this.liveLink,

			isLive: true,

  })

	this.player.attachMediaElement(this.video);

  this.player.load();

  this.player.play();

}
,
		onError: function(error) { 		
	console.error('Error code', error.code, 'object', error);

}
,
	},
	mounted(){
			
  this.playVideo()

	},
	computed: {
	
		selectedQual: function() { 		
	switch(this.quality){

		case '720':

			return ''

		default:

			return '_' + this.quality

	}

}
,
		quality: function() { 		
	return this.$store.state.qualityV

}
,
		liveLink: function() { 		
	return this.$store.state.liveServer + '/live/okikeLive' 

	+ this.selectedQual + '.flv'

}
,
		video: function() { 		
	return this.$refs.videoPlayer.querySelector('video')

}
,
	},
	watch: {
	
		quality: function(val) { 		
	this.flv_destroy()

  this.playVideo()

}
,
	},
	data(){
	return {
		
		playerOptions:  {		
}
,
		player:  null		,
		}
	},
	props: {
	
		isFull: {					required: false,			default: () =>  false,		},
	},
} 
</script>

