<script>

import * as Request from '../../Js/requests'
export default {
	name: 'Sidebar',

    components: {},
	methods: {
	
		goto: function(link){ 		
	this.$router.push(link)

}
,
		logout: function(){ 		
	Request.logout();

	this.closeSideBae();

}
,
	},
	mounted(){
			
	this.selectedLang = this.$store.state.selectedLang

	this.$i18n.locale = this.selectedLang

	},
	computed: {
	
		userImage: function(){ 		
	return (this.userData.image? 

			this.$store.state.apiServer + this.userData.image.url:

			"https://picsum.photos/100")

}
,
		userName: function() { 		
	return this.userData.name

}
,
		darkBg: function() { 		
	return this.$store.state.darkBg

}
,
		isLogged: function() { 		
	return (this.userData != null)

}
,
		moreLink: function(){ 		
	return [

		{text: this.$t('Navigation.profile'), action: this.goto, param: '/profile'},

		{text: this.$t('Navigation.logout'), action: this.logout, param: null},

	]

}
,
		isLogged: function() { 		
	return (this.userData != null)

}
,
		userData: function() { 		
	return this.$store.state.user

}
,
		language: function() { 		
	return ['fr', 'en', 'de']

}
,
		appName: function() { 		
	return this.$store.state.appName

}
,
		secondColor: function() { 		
	return this.$store.state.secondColor

}
,
		navigation: function(){ 		
	return [

		{link: "/", text: this.$t('Navigation.home')},

		{link: "/live", text: this.$t('Navigation.live')},

		{link: "/youtuber", text: this.$t('Navigation.youtuber')},

		{link: "/plan", text: this.$t('Navigation.planning')},

		{link: "/blog", text: this.$t('Navigation.blog')},

		{link: "/about", text: this.$t('Navigation.about')},

	]

}
,
	},
	watch: {
	
		selectedLang: function(val) { 		
	this.$i18n.locale = val

	this.$store.commit('changeSelectedLang', val)

}
,
	},
	data(){
	return {
		
		selectedLang:  'fr'		,
		}
	},
	props: {
	
		closeSideBae: {			type: Function,			required: false,			default: () =>  (()=>{}),		},
	},
} 
</script>

