import router from '../../router'
import store from '../../store'
import axios from 'axios'
import qs from 'qs'

function $axios(){
	return axios.create({
		headers: {
      Authorization: `Bearer ${store.state.token}`,
    }
	})
}

export function signinGoogle(){
	window.location.href = store.state.apiServer + '/connect/google'
}
export function signinFacebook(){
	window.location.href = store.state.apiServer + '/connect/facebook'
}

// Request API.
export function signinProvider(){
	let provider = this.$route.params.provider
	let access_token = this.$route.query.access_token
	
	return axios.get(
      `${store.state.apiServer}/auth/${provider}/callback?access_token=${access_token}`
  )
}

// Request API.
export function sendMailConfirmation(mail){
	return axios
  .post(`${store.state.apiServer}/auth/send-email-confirmation`, {
    email: mail, // user's email
  })
}

// Request API.
export function signup(name, email, pass){
	return axios
	.post(`${store.state.apiServer}/auth/local/register`, {
    username: name,
    email: email,
    password: pass,
  })
} 

// Request API.
export function login(email, pass){
	return axios
	.post(`${store.state.apiServer}/auth/local`, {
    identifier: email,
    password: pass,
  })
} 

// Request API.
export function logout(){
	store.dispatch('logout') 
} 

// Request API.
export function getArchive(titel){
	let local = store.state.selectedLang
	
	return axios
  .get(`${store.state.apiServer}/archives?_locale=${local}&slug_contains=${titel}`)
}

// Request API.
export function getTable(tableTitel){	
	return axios
  .get(`${store.state.apiServer}/${tableTitel}`)
}

// Request API.
export function getLocalTable(tableTitel){
	let local = store.state.selectedLang
	
	return axios
  .get(`${store.state.apiServer}/${tableTitel}?_locale=${local}`)
}

// Request API.
export function getTableArgs(tableTitel, args){
	return axios
  .get(`${store.state.apiServer}/${tableTitel}?${args}`)
}

// Request API.
export function getLocalTableArgs(tableTitel, args){
	let local = store.state.selectedLang
	
	return axios
  .get(`${store.state.apiServer}/${tableTitel}?_locale=${local}&${args}`)
}

// Request the planning to the Maestro
export function getPlanning(){
	return axios.get(`${store.state.maestroServer}/plan`)
}

// Get the informations of a specific categorie
export function getCatInfos(id){
	let local = store.state.selectedLang
	 
	const query = qs.stringify({ _where: { _or: [{ localizations: id }, { id: id }] } });
	
	return axios.get(`${store.state.apiServer}/categories?_locale=${local}&${query}`);
}

export function uploadFile(url){
 	let formData = new FormData();
	formData.append('files', url);

	return $axios().post(store.state.apiServer + '/upload', formData) 	
}

export function updateUserData(attribute, data){
	let apiUrl = store.state.apiServer + '/users' + `/${store.state.user.id}`
	
  let dt = {};
	dt[attribute] = data;

  let config = {
   	url: apiUrl,
		method: "put",
		data: dt
  };
	
  return $axios()(config)
}

export function getVideoInfo(ip) {
  return axios.get(store.state.maestroServer + `/youtube-info/${ip}`);
}
