	computed: {
	
		secondColor: function() { 		
	return this.$store.state.secondColor

}
,
		hours: function(){ 		
	let list = [];

	

	for(let i=0; i<24; i++){	

		let tmp = (i.toString().length == 1? '0' : '') + i + ' : 00'

		list.push(tmp)

	}

	

	return list

}
,
	},
	data(){
