<script>
import Basic from '../../At/Layouts/Basic'

import atController from 'artefact-controller'
import * as Request from '../../Js/requests'
export default {
	name: 'Profile',

    components: {Basic,},
	methods: {
	
		allowChangePass: function() { 		
	this.changePass = true

}
,
		allowChangeNameMail: function() { 		
	this.changeNameMail = true

}
,
		toggelPass: function() { 		
	this.showPass = !this.showPass

}
,
		imageHover: function() { 		
	this.imgHovered = true

}
,
		imageExit: function() { 		
	this.imgHovered = false

}
,
		showError: function(message) { 		
	this.error = message

	this.errorVisible = true	

}
,
		updateImage: function(){			
	Request.uploadFile(this.imgBlob)

	.then((res) => {

		this.imgChanged = false

		Request.updateUserData("image", res.data[0].id)

		.then((res) => {

			this.$store.commit('changeUser', res.data)

		})

  })

  .catch((error) => {

 		console.log(error);

	});

}
,
		updateNameEmail: function(){ 		
	Request.updateUserData("username", this.name)

	.then(() => {

		Request.updateUserData("email", this.email)

		.then((res) => {

			this.$store.commit('changeUser', res.data);

			location.reload();

		})

	});

}
,
		updatePass: function(){ 		
	// check pass

	if(this.pass == null){

		this.showError(this.$t('Signup.enterPass'))		

		return

	}	

	if(this.pass2 == null){

		this.showError(this.$t('Signup.enterPassConf'))		

		return

	}	

	if(this.pass != this.pass2){

		this.showError(this.$t('Signup.samePass'))		

		return

	}	

	Request.updateUserData("pasword", this.pass);

}
,
		pickImage: function(){		
	this.ac.fileOpen({multiple: false, accept: 'image/*' }, (file) => {		

  	const reader = new FileReader();

  	reader.addEventListener('load', (event) => {

    	this.m_image = event.target.result;

			this.imgChanged = true

  	});

  	reader.readAsDataURL(file[0]); 

		this.imgBlob = file[0];

	});

}
,
	},
	mounted(){
			
	this.name = this.user.username;

	this.email = this.user.email;

	},
	computed: {
	
		firstColor: function() { 		
	return this.$store.state.firstColor

}
,
		secondColor: function() { 		
	return this.$store.state.secondColor

}
,
		user: function(){ 		
	return this.$store.state.user ?? ({});

}
,
		passIcon: function(){		
	if(this.showPass){

		return 'ion-ios-eye'

	} else {

		return 'ion-ios-eye-off'

	}

}
,
		image: function(){ 		
	return (this.imgChanged?

		this.m_image:

		(this.user.image? 

			this.$store.state.apiServer + this.user.image.url:

			"https://picsum.photos/400"

		)

	)

}
,
	},
	watch: {
	
		user: function(){ 		
	this.name = this.user.username;

	this.email = this.user.email;

}
,
		name: function(){ 		
	this.nameMailChanged = true;

}
,
		email: function(){ 		
	this.nameMailChanged = true;

}
,
	},
	data(){
	return {
		
		ac:  new atController()		,
		imgHovered:  false		,
		name:  null		,
		email:  null		,
		pass:  null		,
		pass2:  null		,
		imgBlob:  null		,
		errorVisible:  false		,
		error:  "..."		,
		showPass:  false		,
		imgChanged:  false		,
		nameMailChanged:  false		,
		changeNameMail:  false		,
		changePass:  false		,
		}
	},
} 
</script>

