<template>
 <div>  
<div id="LiveZoneSHAKA"> 
         
 <div  :class="'item videoContainer '"   id="videoContainer" ref="videoContainer"  v-bind:style="{}">
<div class="artefactVid"  :class="'networkvideo videoPlayer '"   id="videoPlayer" ref="videoPlayer"  v-bind:style="{}"> <video width="100%" height="100%"  :controls = "false" 
 :preload = "'auto'" 
>
</video> </div>  </div>
 
      </div>
 </div> 
</template>
<script>

import shaka from 'shaka-player';
export default {
	name: 'LiveZoneSHAKA',

    components: {},
	methods: {
	
		playVideo: function() {		
	this.player.configure(this.playerOptions);

	this.player

		.load(this.liveLink)

		.then(() => {

      // This runs if the asynchronous load is successful.

      console.log('The video has now been loaded!');

    })

    .catch(this.onError); // onError is executed if the asynchronous load fails.

	

  this.video.muted = false;

	this.player.setVolume(this.volume/100)

}
,
		onError: function(error) { 		
	console.error('Error code', error.code, 'object', error);

}
,
	},
	mounted(){
			
	this.player = shaka.Player(this.video)

	

	const ui = new shaka.ui.Overlay(

     this.player,

     this.$refs.videoContainer,

     this.$refs.videoPlayer

   );

  ui.getControls();

	

  this.playVideo()

	},
	computed: {
	
		liveLink: function() { 		
	return this.$store.state.liveServer + '/live/okikeLive/index.mpd'

}
,
		video: function() {		
	return this.$refs.videoPlayer;

}
,
	},
	watch: {
	
		volume: function(val) { 		
	this.player.setVolume(val/100)

}
,
	},
	data(){
	return {
		
		playerOptions:  {		
	drm: {

  	servers: {

			 'com.widevine.alpha': "https://widevine-proxy.appspot.com/proxy" 	

		}

	}

}
,
		player:  null		,
		}
	},
	props: {
	
		isFull: {					required: false,			default: () =>  false,		},
		volume: {					required: false,			default: () =>  100,		},
	},
} 
</script>

